<template>
  <div>
    <el-input
      type="textarea"
      v-model="settings"
      placeholder="请输入设定"
      rows="4"
    ></el-input>
    <el-input
      type="textarea"
      v-model="question"
      placeholder="请输入您的问题"
      rows="4"
    ></el-input>
    <el-button type="primary" @click="fetchAnswer">提交</el-button>
    <el-card v-if="answer" class="answer-card">
      <div v-html="formattedAnswer"></div>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios'
import { marked } from 'marked'
import hljs from 'highlight.js'
import 'highlight.js/styles/default.css'
export default {
  data() {
    return {
      settings: 'you are a helpful assistant.',
      question: '',
      answer: '',
      messageHistory: [], // 保存消息历史
    }
  },
  computed: {
    formattedAnswer() {
      // 使用marked将markdown转换为html
      return marked(this.answer, {
        highlight: function (code, lang) {
          const language = hljs.getLanguage(lang) ? lang : 'plaintext'
          return hljs.highlight(code, { language }).value
        },
      })
    },
  },
  methods: {
    async fetchAnswer() {
      this.answer = '正在获取回答...'

      // 构建消息列表
      const messages = [
        { role: 'system', content: this.settings },
        ...this.getLastThreeMessages(), // 获取最近三条消息
        { role: 'user', content: this.question },
      ]

      try {
        const response = await axios.post(
          'https://api.bianxieai.com/v1/chat/completions',
          {
            model: 'claude-3-opus-20240229',
            messages: messages,
          },
          {
            headers: {
              Accept: 'application/json',
              Authorization:
                'Bearer sk-1FfMklIU4FnDvYbS7266011aC38b4452A703481f911d9fE6', // 替换为实际的API Key
              'Content-Type': 'application/json',
            },
          }
        )

        this.answer = response.data.choices[0].message.content

        // 保存本次对话到历史记录
        this.messageHistory.push(
          { role: 'user', content: this.question },
          { role: 'assistant', content: this.answer }
        )

        // 保持历史记录数组的长度为6（3对消息）
        if (this.messageHistory.length > 6) {
          this.messageHistory.splice(0, this.messageHistory.length - 6)
        }

        // 清空提问框
        this.question = ''
      } catch (error) {
        console.error('Error:', error)
        this.answer = '获取回答时出错'
      }
    },
    getLastThreeMessages() {
      // 返回最近三对消息（最多六条消息）
      return this.messageHistory.slice(-6)
    },
  },
}
</script>

<style scoped>
.el-input {
  margin-bottom: 20px;
}
.el-button {
  margin: 20px auto;
}
.answer-card {
  margin-top: 20px;
  white-space: pre-wrap; /* 保持换行格式 */
  text-align: left;
}
</style>
